/**
 * @jest-environment jsdom
 */
import { renderHook, act } from '@testing-library/react';

import AsyncStorage from '@react-native-async-storage/async-storage';
import useActivityMetrics from '../../src/hooks/useActivityMetrics';

// Mock AsyncStorage
jest.mock('@react-native-async-storage/async-storage', () => ({
  setItem: jest.fn(() => Promise.resolve()),
  getItem: jest.fn(() => Promise.resolve(null)),
  removeItem: jest.fn(() => Promise.resolve()),
}));

describe('useActivityMetrics', () => {
  const MOCK_DATE = new Date('2025-01-01T12:00:00.000Z');
  const MOCK_TODAY_STRING = MOCK_DATE.toDateString();

  beforeEach(() => {
    jest.useFakeTimers();
    jest.setSystemTime(MOCK_DATE);
    AsyncStorage.getItem.mockClear();
    AsyncStorage.setItem.mockClear();
  });

  afterEach(() => {
    jest.runOnlyPendingTimers();
    jest.useRealTimers();
  });

  it('initializes with default values and loads metrics', async () => {
    AsyncStorage.getItem.mockResolvedValueOnce(JSON.stringify({ someMetric: 1 }));

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());

    expect(result.current.isLoading).toBe(true);
    expect(result.current.currentStreak).toBe(0);
    expect(result.current.todayMinutes).toBe(0);
    expect(result.current.formattedTime).toBe('0min');

    await waitForNextUpdate();

    expect(result.current.isLoading).toBe(false);
    expect(result.current.metrics).toEqual({ someMetric: 1 });
    expect(AsyncStorage.getItem).toHaveBeenCalledWith('activity_metrics');
  });

  it('starts and ends a session, updating todayMinutes and saving', async () => {
    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate(); // Wait for initial load

    act(() => {
      result.current.startSession();
    });

    jest.advanceTimersByTime(120 * 1000); // Advance by 2 minutes

    await act(async () => {
      await result.current.endSession();
    });

    expect(result.current.todayMinutes).toBe(2);
    expect(result.current.formattedTime).toBe('2min');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '2');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_time_date', MOCK_TODAY_STRING);
  });

  it('updates streak for a consecutive day', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null) // No initial metrics
      .mockResolvedValueOnce(JSON.stringify({ currentStreak: 1 })) // Mock initial streak
      .mockResolvedValueOnce(new Date(MOCK_DATE.getTime() - 24 * 60 * 60 * 1000).toDateString()); // last_activity_date was yesterday

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate(); // Initial load

    act(() => {
      result.current.updateStreak();
    });

    await waitForNextUpdate(); // Wait for streak update

    expect(result.current.currentStreak).toBe(2);
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '2');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_activity_date', MOCK_TODAY_STRING);
  });

  it('resets streak if not consecutive day', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null) // No initial metrics
      .mockResolvedValueOnce(JSON.stringify({ currentStreak: 5 })) // Mock initial streak
      .mockResolvedValueOnce(new Date(MOCK_DATE.getTime() - 48 * 60 * 60 * 1000).toDateString()); // last_activity_date was 2 days ago

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate(); // Initial load

    act(() => {
      result.current.updateStreak();
    });

    await waitForNextUpdate(); // Wait for streak update

    expect(result.current.currentStreak).toBe(1);
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '1');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_activity_date', MOCK_TODAY_STRING);
  });

  it('does not update streak if already updated today', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null) // No initial metrics
      .mockResolvedValueOnce(JSON.stringify({ currentStreak: 3 })) // Mock initial streak
      .mockResolvedValueOnce(MOCK_TODAY_STRING); // last_activity_date is today

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate(); // Initial load

    act(() => {
      result.current.updateStreak();
    });

    await waitForNextUpdate(); // Wait for streak update

    expect(result.current.currentStreak).toBe(3); // Should remain unchanged
    expect(AsyncStorage.setItem).not.toHaveBeenCalledWith('current_streak', expect.any(String));
  });

  it('returns correct streak trend', async () => {
    const { result, waitForNextUpdate, rerender } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate(); // Initial load

    act(() => {
      result.current.updateStreak(); // Set streak to 1
    });
    await waitForNextUpdate();
    expect(result.current.streakTrend).toBe('ðŸ”¥ Continue!');

    act(() => {
      // Manually set streak for testing purposes, in real app this would be via updateStreak
      result.current.currentStreak = 3;
    });
    rerender();
    expect(result.current.streakTrend).toBe('ðŸ’ª En forme!');

    act(() => {
      result.current.currentStreak = 7;
    });
    rerender();
    expect(result.current.streakTrend).toBe('ðŸ† Incroyable!');

    act(() => {
      result.current.currentStreak = 0;
    });
    rerender();
    expect(result.current.streakTrend).toBeNull();
  });

  it('returns correct formatted time', async () => {
    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate(); // Initial load

    act(() => {
      result.current.startSession();
    });

    jest.advanceTimersByTime(30 * 60 * 1000); // 30 minutes
    await act(async () => { await result.current.endSession(); });
    expect(result.current.formattedTime).toBe('30min');

    act(() => {
      result.current.startSession();
    });
    jest.advanceTimersByTime(60 * 60 * 1000); // 60 minutes (1 hour)
    await act(async () => { await result.current.endSession(); });
    expect(result.current.formattedTime).toBe('1h');

    act(() => {
      result.current.startSession();
    });
    jest.advanceTimersByTime(75 * 60 * 1000); // 75 minutes (1h 15min)
    await act(async () => { await result.current.endSession(); });
    expect(result.current.formattedTime).toBe('2h15min'); // 30 + 60 + 75 = 165 minutes total
  });
});
